
.poiDetailBg{position:fixed;z-index:3;left:0;top:0;width:100%;height:100%;opacity:.5;background:#000
}
.poiDetail{position:absolute;top:20px;left:50%;z-index:2000;width:960px;margin-left:-480px;margin-bottom:30px;background:#fff
}
.poiDetail .header{height:51px;border-bottom:1px solid #e2e2e2
}
.poiDetail .header .tit{float:left;margin-left:15px;line-height:50px;font-size:16px
}
.poiDetail .header .tabs{float:left
}
.poiDetail .header .tabs li{float:left;margin-left:30px
}
.poiDetail .header .tabs li.line{float:left;width:0;height:28px;margin-top:11px;border-right:1px solid #e2e2e2
}
.poiDetail .header .tabs li:last-child a{border-right:0
}
.poiDetail .header .tabs li a{display:block;height:40px;margin-top:11px;line-height:28px;font-size:16px;color:#8F9BA2
}
.poiDetail .header .tabs li a.active{border-bottom:2px solid #23a16d;color:#444
}
.poiDetail .header .btns{float:right;margin:8px 20px 0 0
}
.poiDetail .header .btns .el-button{border-color:#23a16d;color:#23a16d
}
.poiDetail .header .closeBtn{float:right;width:50px;height:50px;border-left:1px solid #e2e2e2;line-height:50px;text-align:center
}
.poiDetail .header .closeBtn i{font-size:20px;font-weight:bold;color:#A1ACB3
}
.poiDetail .header .closeBtn:hover{background:#f1f1f1
}
.poiDetail .con{padding:20px 30px
}
.poiDetail .con.baseInfo .title{border-bottom:1px solid #e2e2e2
}
.poiDetail .con.baseInfo .title h2{float:left;font-weight:normal;font-size:20px;color:#23a16d
}
.poiDetail .con.baseInfo .title .edit{float:right;color:#23a16d;font-size:14px
}
.poiDetail .con.baseInfo .title .edit i{margin-right:5px;font-size:13px
}
.poiDetail .con.baseInfo .title .subTit{clear:both;overflow:hidden;padding:15px 0
}
.poiDetail .con.baseInfo .title .subTit label{float:left;width:85px;color:#999
}
.poiDetail .con.baseInfo .detail{overflow:hidden
}
.poiDetail .con.baseInfo .detail .detailList{float:left;width:605px;padding-right:30px;border-right:1px solid #e2e2e2
}
.poiDetail .con.baseInfo .detail .detailList li{padding:15px 0;overflow:hidden;border-bottom:1px solid #e2e2e2
}
.poiDetail .con.baseInfo .detail .detailList li label{float:left;width:85px;color:#999
}
.poiDetail .con.baseInfo .detail .detailList li .txt{margin-left:85px
}
.poiDetail .con.baseInfo .mapInfo{float:right;width:275px
}
.poiDetail .con.baseInfo .mapInfo .tit{margin-top:15px;margin-bottom:5px;line-height:24px;font-size:14px
}
.poiDetail .con.baseInfo .mapInfo .mapWrap{position:relative;width:277px;height:277px;border:1px solid #e2e2e2
}
.poiDetail .con.baseInfo .mapInfo .mapWrap .mapPoi{width:275px;height:275px
}
.poiDetail .con.baseInfo .mapInfo .mapWrap .openBtn{position:absolute;bottom:0;left:0;width:100%;height:34px;background:rgba(0,0,0,0.25);text-align:center;line-height:34px;color:#fff
}
.poiDetail .con.baseInfo .mapInfo .mapWrap .openBtn:hover{background:rgba(0,0,0,0.4)
}
.poiDetail .con.baseInfo .mapInfo .address{margin-top:10px;color:#999
}
.poiDetail .con.demoInfo .pic{width:100%;height:360px;text-align:center;background-size:cover;background-position:center
}
.poiDetail .con.demoInfo .title{margin-top:10px
}
.poiDetail .con.demoInfo .title h2{font-weight:normal;font-size:20px;color:#23a16d
}
.poiDetail .con.demoInfo .des{margin-top:5px;line-height:24px
}
.poiDetail .con.demoInfo .detail{margin-top:15px;padding-top:10px;border-top:1px solid #e2e2e2
}
.poiDetail .con.demoInfo .detail li{overflow:hidden;padding:8px 0
}
.poiDetail .con.demoInfo .detail li label{float:left;width:85px;color:#999
}
.poiDetail .con.demoInfo .detail li .txt{margin-left:85px
}
.poiDetail .footer{height:60px;background:#E2E2E2
}
.poiDetail .footer .btns{float:right;margin:14px 30px 0 0
}
.poiDetail .footer .btns .el-button{width:70px;margin-left:5px
}
.bigMap .bigMapBg{display:none;position:fixed;z-index:9999;left:0;top:0;width:100%;height:100%;opacity:.3;background:#000
}
.bigMap .bigMapBg.show{display:block
}
.bigMap .mapWrap{position:absolute;z-index:10000;left:0;top:0;bottom:50px;width:100%;max-height:590px;-webkit-trafficion:transform .2s;-moz-trafficion:transform .2s;-o-trafficion:transform .2s;trafficion:transform .2s;transform:translateY(-120%);-moz-transform:translateY(-120%)
}
.bigMap .mapWrap.show{transform:translateY(0);-moz-transform:translateY(0)
}
.bigMap .mapWrap .map{position:absolute;top:0;left:0;bottom:0;width:100%;height:100%
}
.bigMap .mapWrap .closeBigMap{display:block;position:absolute;z-index:10001;left:50%;bottom:-40px;margin-left:-40px;width:80px;height:80px;border-radius:50%;background:#fff;box-sizing:border-box;text-align:center;line-height:85px;box-shadow:rgba(0,0,0,0.2) 0 1px 2px 0
}
.bigMap .mapWrap .closeBigMap:hover{background:#f1f1f1
}
.bigMap .mapWrap .closeBigMap i{font-size:30px;color:#999
}
.mapPoiMarker{padding:10px;font-size:14px
}
.mapPoiMarker .tit{padding-bottom:5px;font-weight:bold
}


