
.poiDetailBg{position:fixed;z-index:3;left:0;top:0;width:100%;height:100%;opacity:.5;background:#000
}
.poiDetail{position:absolute;top:20px;left:50%;z-index:2000;width:960px;margin-left:-480px;margin-bottom:30px;background:#fff
}
.poiDetail .header{height:51px;border-bottom:1px solid #e2e2e2
}
.poiDetail .header .tit{float:left;margin-left:15px;line-height:50px;font-size:16px
}
.poiDetail .header .tabs{float:left
}
.poiDetail .header .tabs li{float:left;margin-left:30px
}
.poiDetail .header .tabs li.line{float:left;width:0;height:28px;margin-top:11px;border-right:1px solid #e2e2e2
}
.poiDetail .header .tabs li:last-child a{border-right:0
}
.poiDetail .header .tabs li a{display:block;height:40px;margin-top:11px;line-height:28px;font-size:16px;color:#8F9BA2
}
.poiDetail .header .tabs li a.active{border-bottom:2px solid #23a16d;color:#444
}
.poiDetail .header .btns{float:right;margin:8px 20px 0 0
}
.poiDetail .header .btns .el-button{border-color:#23a16d;color:#23a16d
}
.poiDetail .header .closeBtn{float:right;width:50px;height:50px;border-left:1px solid #e2e2e2;line-height:50px;text-align:center
}
.poiDetail .header .closeBtn i{font-size:20px;font-weight:bold;color:#A1ACB3
}
.poiDetail .header .closeBtn:hover{background:#f1f1f1
}
.poiDetail .con{padding:20px 30px
}
.poiDetail .con.productInfo .baseInfo{overflow:hidden
}
.poiDetail .con.productInfo .baseInfo .mainPic{float:left;width:380px;height:360px;text-align:center;background-size:cover;background-position:center
}
.poiDetail .con.productInfo .baseInfo .info{margin-left:400px
}
.poiDetail .con.productInfo .baseInfo .info .tit{font-size:18px;font-weight:bold;color:#23a16d
}
.poiDetail .con.productInfo .baseInfo .info .subTit{height:24px;line-height:24px;color:#999
}
.poiDetail .con.productInfo .baseInfo .info .infoList{overflow:hidden;padding:8px 0 10px;border-bottom:1px solid #e2e2e2
}
.poiDetail .con.productInfo .baseInfo .info .infoList li{float:left;margin-right:20px;padding-right:15px;border-right:1px solid #e2e2e2;line-height:18px;font-size:14px
}
.poiDetail .con.productInfo .baseInfo .info .infoList li:last-child{border-right:0
}
.poiDetail .con.productInfo .baseInfo .info .selectOpts{margin-top:10px;overflow:hidden
}
.poiDetail .con.productInfo .baseInfo .info .selectOpts li{padding:10px 0
}
.poiDetail .con.productInfo .baseInfo .info .selectOpts li label{float:left;width:75px;line-height:32px;color:#999;font-size:14px
}
.poiDetail .con.productInfo .baseInfo .info .selectOpts li .opts{margin-left:80px
}
.poiDetail .con.productInfo .baseInfo .info .selectOpts li .opts .selItem{float:left;height:32px;margin-right:10px;padding:0 15px;border:1px solid #DEE3E6;line-height:30px;overflow:hidden
}
.poiDetail .con.productInfo .baseInfo .info .selectOpts li .opts .selItem.active{position:relative;border:2px solid #23a16d;line-height:28px
}
.poiDetail .con.productInfo .baseInfo .info .selectOpts li .opts .selItem.active:after{font-family:element-icons !important;content:"\E611";position:absolute;right:0;bottom:0;color:#fff;font-size:12px;line-height:8px
}
.poiDetail .con.productInfo .baseInfo .info .selectOpts li .opts .selItem.active:before{content:"";position:absolute;right:-6px;bottom:-12px;width:15px;height:35px;background:#23a16d;transform:rotate(50deg)
}
.poiDetail .con.productInfo .baseInfo .info .selectOpts li .opts .price{line-height:32px;font-size:14px;font-weight:bold;color:#23a16d
}
.poiDetail .con.productInfo .baseInfo .info .selectOpts li .opts .price strong{font-size:20px
}
.poiDetail .con.productInfo .detailInfo{margin-top:40px
}
.poiDetail .con.productInfo .detailInfo .tabs{height:45px;background:#EEF1F3;border-bottom:1px solid #E5EAEE
}
.poiDetail .con.productInfo .detailInfo .tabs li{float:left;margin-right:60px;padding:0 10px;line-height:43px;font-size:14px;cursor:pointer
}
.poiDetail .con.productInfo .detailInfo .tabs li.active{border-bottom:2px solid #23a16d;color:#23a16d
}
.poiDetail .con.productInfo .detailInfo .detailCon{line-height:24px;font-size:14px
}
.poiDetail .con.productInfo .detailInfo .detailCon .detailTit{margin:20px 0 10px;padding-left:8px;line-height:14px;font-size:14px;border-left:4px solid #23a16d
}
.poiDetail .footer{height:60px;background:#E2E2E2
}
.poiDetail .footer .btns{float:right;margin:14px 30px 0 0
}
.poiDetail .footer .btns .el-button{width:70px;margin-left:5px
}


