
.customerBox{position:relative;height:600px
}
.customerBox .leftBox{position:absolute;left:0;top:0;bottom:0;width:220px;padding:5px 10px;background:#F7FAFB;border:1px solid #E1E5ED
}
.customerBox .leftBox ul{border-bottom:1px dotted #B6BDC6
}
.customerBox .leftBox ul.groupList li{cursor:pointer
}
.customerBox .leftBox ul.groupList li:hover,.customerBox .leftBox ul.groupList li.active{background:#EBF0F2
}
.customerBox .leftBox ul li{padding:0 5px;height:36px;line-height:35px;cursor:pointer
}
.customerBox .leftBox ul li:hover{background:#EBF0F2
}
.customerBox .leftBox ul li.newGroupLink{cursor:pointer
}
.customerBox .leftBox ul li.newGroupLink:hover{color:#0079f5
}
.customerBox .leftBox ul li .opts{float:right
}
.customerBox .leftBox ul li .opts a{color:#95A7B0
}
.customerBox .leftBox ul li .opts a.edit{display:inline-block;vertical-align:top;margin-right:5px
}
.customerBox .leftBox ul li .opts a.edit .icon-bianji-blue{font-size:12px
}
.customerBox .leftBox ul li .opts a:hover{color:#0079f5
}
.customerBox .leftBox ul li .opts .icon-shoucang{color:#FF6767
}
.customerBox .leftBox ul li .otherOpts{display:none
}
.customerBox .leftBox ul li:hover .otherOpts{display:block
}
.customerBox .leftBox ul li .openBtn{transform:rotate(-90deg);cursor:pointer
}
.customerBox .leftBox ul li .openBtn.groupClose{transform:rotate(90deg)
}
.customerBox .leftBox ul li .editInput .el-input__inner{border-right:0
}
.customerBox .leftBox ul li .editInput .el-input__inner:focus,.customerBox .leftBox ul li .editInput .el-input__inner:hover{border-color:#e2e2e2
}
.customerBox .leftBox ul li .editInput .el-input-group__append{background:#fff !important
}
.customerBox .leftBox ul li .editInput .el-input-group__append .el-button{padding:0 10px;font-size:14px
}
.customerBox .leftBox ul li .editInput .el-input-group__append .el-button:hover{color:#0079f5
}
.customerBox .rightBox{position:relative;margin-left:240px
}
.customerBox .rightBox .optBtns{margin-bottom:10px;overflow:hidden
}
.customerBox .rightBox .optBtns .btns{float:left
}
.customerBox .rightBox .optBtns .btns .el-button{width:100px
}
.customerBox .rightBox .optBtns .btns .selectGroup .el-input__inner{width:100px;padding-left:15px
}
.customerBox .rightBox .optBtns .btns .selectGroup .el-icon-arrow-up:before{content:"\E60C";color:#606266
}
.customerBox .rightBox .optBtns .searchBox{float:right;width:210px
}
.customerBox .rightBox .optBtns .searchBox .el-input__inner{padding-left:15px;border-radius:16px 0 0 16px;border-right:0
}
.customerBox .rightBox .optBtns .searchBox .el-input-group__append{border-radius:0 16px 16px 0;background:none
}
.customerBox .rightBox .addCustomerTip{position:absolute;top:120px;left:0;width:100%;text-align:center
}
.customerBox .rightBox .addCustomerTip .icon{width:175px;height:175px;margin:0 auto;background:url(../../assets/images/icon_no_customer.png)
}
.customerBox .rightBox .addCustomerTip .txt{margin:15px 0;color:#ddd
}
.customerBox .rightBox .noDataTip{position:absolute;top:120px;left:0;width:100%;text-align:center
}
.customerBox .rightBox .noDataTip a{color:#0079f5
}
.customerBox .rightBox .noDataTip a i{font-size:12px
}
.iconFav{color:#FF6767
}


