
.mainNav{float:left;width:220px
}
.mainNav .logo{width:220px;height:60px;background:url(../../assets/images/logo.png) 15px 10px no-repeat #16232c
}
.mainNav .user{margin-top:45px;text-align:center
}
.mainNav .user .avatar{width:98px;height:98px;margin:0 auto;border-radius:100%
}
.mainNav .user .avatar .pic{width:98px;height:98px;background-size:cover;background-position:center;border-radius:100%
}
.mainNav .user .name{margin-top:5px;font-size:16px;color:#c1cfe0;font-weight:bold
}
.mainNav .user .phone{color:#576773
}
.mainNav ul{margin-top:12px;padding:10px 0 12px;border-top:1px solid #161E25;border-bottom:1px solid #161E25
}
.mainNav ul li{height:50px;margin-top:2px;line-height:50px;font-size:14px;color:#c1cfe0;cursor:pointer
}
.mainNav ul li:active,.mainNav ul li:hover{background:#101c24
}
.mainNav ul li i{float:left;width:20px;height:20px;margin:15px 15px 15px 20px;background:url(../../assets/images/icon_main_nav.png)
}
.mainNav ul li .icon1{background-position:0 0
}
.mainNav ul li .icon2{background-position:0 -20px
}
.mainNav ul li .icon3{background-position:0 -40px
}
.mainNav ul li .icon4{background-position:0 -60px
}
.mainNav ul li .icon5{background-position:0 -80px
}
.mainNav ul li .icon6{background-position:0 -100px
}
.mainNav ul li .num{float:right;height:16px;margin-top:17px;margin-right:16px;padding:0 5px;background:#49ABD6;border-radius:8px;line-height:16px;font-size:12px;color:#fff
}
.mainNav ul li:nth-child(4) .num{background:#26C24C
}
.mainNav ul li.active{background:#101c24
}


