
.publishPage .topBar{height:60px;background:#253744;box-shadow:0 3px 10px rgba(0,0,0,0.04)
}
.publishPage .topBar .backBtn{float:left;width:95px;height:24px;margin-top:18px;background:url(../../assets/images/icon_back.png) center center no-repeat;border-right:1px solid #4a5e6d
}
.publishPage .topBar .typeSel{float:left;margin-top:14px;margin-left:20px;line-height:32px
}
.publishPage .topBar .typeSel label{float:left;font-size:16px
}
.publishPage .topBar .typeSel .templateType{float:left;border:0
}
.publishPage .topBar .typeSel .templateType .el-input{width:155px
}
.publishPage .topBar .typeSel .templateType .el-input__inner{border:0;font-size:16px
}
.publishPage .topBar .typeSel .templateType .el-icon-arrow-up:before{content:"\E60B"
}
.publishPage .topBar .typeSel .templateType .el-input .el-select__caret{transform:none
}
.publishPage .topBar .typeSel .templateName{float:left;margin-left:10px;font-size:16px;color:#cde0ed
}
.publishPage .topBar .stepInfo{position:absolute;width:480px;left:50%;margin-left:-300px;margin-top:10px;line-height:32px
}
.publishPage .topBar .stepInfo li{position:relative;float:left;width:120px
}
.publishPage .topBar .stepInfo li:first-child:before{border-radius:2px 0 0 2px
}
.publishPage .topBar .stepInfo li:last-child:before{border-radius:0 2px 2px 0
}
.publishPage .topBar .stepInfo li.active:before{background:#23a16d
}
.publishPage .topBar .stepInfo li.active span{background:#23a16d
}
.publishPage .topBar .stepInfo li.active p{color:#23a16d
}
.publishPage .topBar .stepInfo li:before{content:'';position:absolute;left:0;top:10px;width:150px;height:5px;background:#DAE5E6
}
.publishPage .topBar .stepInfo li span{display:block;position:relative;z-index:1;width:22px;height:22px;margin:0 auto;background:#DAE5E6;border-radius:100%;line-height:22px;text-align:center;color:#fff
}
.publishPage .topBar .stepInfo li p{position:relative;z-index:1;line-height:24px;text-align:center;color:#DAE5E6
}
.publishPage .topBar .optBtns{float:right;height:60px;padding:0 35px;border-left:1px solid #DADEE5
}
.publishPage .topBar .optBtns a{display:block;line-height:60px;color:#555
}
.publishPage .topBar .optBtns a:hover{color:#23a16d
}
.publishPage .topBar .optBtns a i{font-size:24px
}
.publishPage .publishCon{margin:0 auto
}
.publishPage .publishCon .title{width:800px;margin:20px auto;border-bottom:1px solid #D9DFE9;line-height:100px;font-size:24px;text-align:center
}
.publishPage .publishCon .tips{text-align:center;font-size:14px;color:#a0abb3
}
.publishPage .publishCon .types{width:945px;margin:0 auto
}
.publishPage .publishCon .types li{float:left;width:175px;margin:50px 70px;cursor:pointer
}
.publishPage .publishCon .types li .icon{height:210px;padding:10px 11px 0;background:#fff;box-shadow:0 5px 15px rgba(0,0,0,0.08)
}
.publishPage .publishCon .types li .icon:hover{box-shadow:0 5px 20px rgba(0,0,0,0.2)
}
.publishPage .publishCon .types li .icon p{line-height:44px;text-align:center;font-size:18px;font-weight:bold
}
.publishPage .publishCon .types li .des{margin-top:20px;font-size:14px;color:#a0abb3
}
.shareUrl .tit{line-height:24px
}
.shareUrl .tit i{margin-right:3px;vertical-align:middle;font-size:20px
}
.shareUrl .tip{margin-bottom:5px;line-height:24px;color:#a0abb3
}


