
.routeTemplateBg{position:fixed;z-index:3;left:0;top:0;width:100%;height:100%;opacity:.5;background:#000
}
.routeTemplate{position:absolute;top:20px;left:50%;z-index:2000;width:960px;margin-left:-480px;margin-bottom:30px;background:#fff
}
.routeTemplate .header{height:51px;border-bottom:1px solid #e2e2e2
}
.routeTemplate .header .tit{float:left;margin-left:15px;line-height:50px;font-size:16px
}
.routeTemplate .header .btns{float:right;margin:8px 20px 0 0
}
.routeTemplate .header .btns .el-button{border-color:#23a16d;color:#23a16d
}
.routeTemplate .header .closeBtn{float:right;width:50px;height:50px;border-left:1px solid #e2e2e2;line-height:50px;text-align:center
}
.routeTemplate .header .closeBtn i{font-size:20px;font-weight:bold;color:#A1ACB3
}
.routeTemplate .header .closeBtn:hover{background:#f1f1f1
}
.routeTemplate .templateBox{margin:0 20px;background:#fff
}
.routeTemplate .templateBox .tabs{height:53px;border-bottom:1px solid #EAEDEE
}
.routeTemplate .templateBox .tabs li{float:left;padding:15px 0 12px
}
.routeTemplate .templateBox .tabs li a{display:block;padding:0 24px;border-right:1px solid #EAEDEE;line-height:24px;font-size:14px;color:#555
}
.routeTemplate .templateBox .tabs li.active{border-bottom:2px solid #23a16d
}
.routeTemplate .templateBox .tabs li.active a{color:#23a16d
}
.routeTemplate .templateBox .tabs li:last-child a{border-right:0
}
.routeTemplate .filterBox{height:59px;padding:12px 0;border-bottom:1px solid #EAEDEE
}
.routeTemplate .filterBox .filterKeywords{float:left;width:215px;height:34px;border-radius:34px;background:#EDF1F2
}
.routeTemplate .filterBox .filterKeywords input{height:34px;padding-left:17px;line-height:34px;background:none;border:0
}
.routeTemplate .filterBox .filterKeywords .btnSubmit{float:right;width:45px;height:34px;background:url(../../assets/images/icon_search.png) center center no-repeat
}
.routeTemplate .filterBox .filterKeywords:active,.routeTemplate .filterBox .filterKeywords:hover{background:#eee
}
.routeTemplate .filterBox .dayFilter{float:right;padding:0 15px;font-size:14px
}
.routeTemplate .filterBox .dayFilter .selectBox{display:inline-block;vertical-align:middle
}
.routeTemplate .filterBox .dayFilter .selectBox .list li{padding:5px 10px;line-height:22px
}
.routeTemplate .templateCon{background:#fff
}
.routeTemplate .routeNav{float:left;width:215px;height:500px;overflow-y:auto;background:#fff;border-right:1px solid #EBEEF2
}
.routeTemplate .routeNav li{position:relative;height:72px;padding:12px 40px 12px 10px;border-bottom:1px solid #EBEEF2
}
.routeTemplate .routeNav li .tit{font-size:14px
}
.routeTemplate .routeNav li .checkboxC{cursor:pointer;display:none;position:absolute;right:16px;top:16px
}
.routeTemplate .routeNav li .checkboxC.active{display:block
}
.routeTemplate .routeNav li.active{background:#F7F8F9
}
.routeTemplate .routeNav li:hover{background:#F7F8F9
}
.routeTemplate .routeNav li:hover .checkboxC{display:block
}
.routeTemplate .routeDayDetail{height:500px;margin-left:230px;overflow-y:auto;background:#fff
}
.routeTemplate .routeDayDetail .routeDetailHeader{height:50px;border-bottom:1px solid #EBEEF2;line-height:50px
}
.routeTemplate .routeDayDetail .routeDetailHeader .tit{float:left;color:#bac3c9;font-size:14px
}
.routeTemplate .routeDayDetail .routeDetailHeader .checkbox{float:right;margin-right:15px;margin-top:-2px
}
.routeTemplate .routeDayDetail .routeDetailHeader .checkbox label{display:inline-block;margin-right:10px;font-size:14px
}
.routeTemplate .routeDayDetail .routeDetailHeader .checkbox .checkboxC{display:inline-block;vertical-align:middle
}
.routeTemplate .routeDayDetail .templateDayList li{position:relative;padding:10px 65px 15px 0;border-bottom:1px solid #EBEEF2
}
.routeTemplate .routeDayDetail .templateDayList li .checkboxC{position:absolute;right:15px;top:15px
}
.routeTemplate .routeDayDetail .templateDayList li .tit{font-size:16px
}
.routeTemplate .routeDayDetail .templateDayList li .tit span{margin-left:5px;font-size:14px
}
.routeTemplate .routeDayDetail .templateDayList li .txt{margin-top:10px;line-height:22px;font-size:13px;color:#828d94
}
.routeTemplate .footer{height:60px;background:#E2E2E2
}
.routeTemplate .footer .btns{float:right;margin:14px 30px 0 0
}
.routeTemplate .footer .btns .el-button{width:70px;margin-left:5px
}


