
.customerInfoBox{position:relative;margin-top:20px
}
.customerInfoBox .leftBox{position:absolute;left:0;top:0;width:220px;padding:5px 10px
}
.customerInfoBox .leftBox .avatar{width:180px;padding:10px;margin:0 auto;box-shadow:0 0 8px rgba(0,0,0,0.15)
}
.customerInfoBox .leftBox .avatar .pic{width:160px;height:160px;background-size:cover;background-position:center
}
.customerInfoBox .leftBox .uploadCustomerPic{text-align:center;margin-top:15px
}
.customerInfoBox .leftBox .uploadCustomerPic .el-button{background:none;border:0;color:#a0abb3
}
.customerInfoBox .rightBox{overflow:hidden;width:600px;margin-left:220px
}
.customerInfoBox .rightBox .optBtns{margin-bottom:10px;overflow:hidden
}
.customerInfoBox .rightBox .optBtns .btns{float:left
}
.customerInfoBox .rightBox .optBtns .searchBox{float:right;width:210px
}
.customerInfoBox .rightBox .optBtns .searchBox .el-input__inner{border-radius:16px
}
.customerInfoBox .rightBox .customerForm .el-form-item__label{color:#a0abb3
}


