
.delTipCon{padding:25px 0;text-align:center
}
.delTipCon p{line-height:24px
}
.delTipCon .colorRed{color:#F56C6C
}
.routeListTable td{padding:10px 0;color:#9FA5B0;cursor:pointer
}
.tableDataDel{font-size:18px;color:#9FA5B0
}
.routeTit .avatar{float:left;width:90px;height:70px;background-size:cover;background-position:center
}
.routeTit h3{margin-left:105px;font-size:14px;font-weight:normal;color:#444
}
.routeTit .cityList{margin-top:20px;margin-left:105px
}
.routeTit .cityList div{width:100%;overflow:hidden;white-space:nowrap;text-overflow:ellipsis
}
.addNewRouteTip{width:170px;margin:55px auto;text-align:center;font-size:14px
}
.addNewRouteTip .pic{width:170px;height:215px;border:1px dashed #e3e3e3;cursor:pointer
}
.addNewRouteTip .pic:hover{background:#F9FAFA
}
.addNewRouteTip .pic .icon{width:68px;height:91px;margin:47px auto 0;background:url(../../assets/images/icon_add.png)
}
.addNewRouteTip .pic .txt{line-height:40px
}
.addNewRouteTip p{margin-top:15px;color:#C7CBD1
}
.formAdd{margin-bottom:15px
}
.formAdd .label{display:block;padding-right:12px;font-size:13px;text-align:right;color:#474747
}
.formAdd .dateRange{width:195px;padding:0 5px
}
.formAdd .el-range__icon{line-height:24px
}
.formAdd .el-range-separator{width:10%;padding:0 2px;line-height:30px;vertical-align:top;font-size:12px
}
.formAdd .el-range__close-icon{display:none
}
.formAdd .el-range-input{width:38%;vertical-align:middle
}
.formAdd .addPerson{height:34px;padding-left:12px;background:#fafafa;line-height:34px;color:#999;cursor:pointer
}
.formAdd .addPerson i{font-size:14px
}
.formAdd .addPerson:hover{background:#fff
}
.formAdd .citySel .el-input__prefix{display:none
}
.formAdd .citySel .el-input__inner{width:195px;padding-left:8px
}
.formAdd .personList li{margin-bottom:10px
}
.formAdd .personList li .done{line-height:32px
}
.formAdd .personList li .iconEdit{display:inline-block;vertical-align:middle;width:24px;height:24px;margin-top:-6px;background:url(../../assets/images/icon_edit.png) center center no-repeat
}
.formAdd .personList li .inputText{width:185px;margin-right:10px
}
.formAdd .personList li .btn{float:right;width:32px;height:32px;margin-left:5px;padding:5px;background:#e2e2e2;border:0;font-size:20px
}
.formAdd .personList li .btn:hover{background:#ddd
}
.formAdd .personList li .btnDel:hover{color:#F56C6C
}
.formAdd .personList li .btnAdd{color:#23a16d
}
.formAdd .templateType{width:100%
}


