
.routeExportTemplateBg{position:fixed;z-index:3;left:0;top:0;width:100%;height:100%;opacity:.5;background:#000
}
.routeExportTemplate{position:absolute;top:20px;left:50%;z-index:2000;width:960px;margin-left:-480px;margin-bottom:30px;background:#fff
}
.routeExportTemplate .header{height:51px;border-bottom:1px solid #e2e2e2
}
.routeExportTemplate .header .tit{float:left;margin-left:15px;line-height:50px;font-size:16px
}
.routeExportTemplate .header .btns{float:right;margin:8px 20px 0 0
}
.routeExportTemplate .header .btns .el-button{border-color:#23a16d;color:#23a16d
}
.routeExportTemplate .header .closeBtn{float:right;width:50px;height:50px;border-left:1px solid #e2e2e2;line-height:50px;text-align:center
}
.routeExportTemplate .header .closeBtn i{font-size:20px;font-weight:bold;color:#A1ACB3
}
.routeExportTemplate .header .closeBtn:hover{background:#f1f1f1
}
.routeExportTemplate .templateBox{margin:0 20px;background:#fff
}
.routeExportTemplate .templateBox .tabs{height:53px;border-bottom:1px solid #EAEDEE
}
.routeExportTemplate .templateBox .tabs li{float:left;padding:15px 0 12px
}
.routeExportTemplate .templateBox .tabs li a{display:block;padding:0 24px;border-right:1px solid #EAEDEE;line-height:24px;font-size:14px;color:#555
}
.routeExportTemplate .templateBox .tabs li.active{border-bottom:2px solid #23a16d
}
.routeExportTemplate .templateBox .tabs li.active a{color:#23a16d
}
.routeExportTemplate .templateBox .tabs li:last-child a{border-right:0
}
.routeExportTemplate .templateCon{background:#fff
}
.routeExportTemplate .routeNav{float:left;width:280px;height:500px;overflow-y:auto;background:#fff;border-right:1px solid #EBEEF2
}
.routeExportTemplate .routeNav .routeNavHeader{height:50px;border-bottom:1px solid #EBEEF2
}
.routeExportTemplate .routeNav .routeNavHeader .tit{float:left;padding-left:10px;line-height:50px;color:#bac3c9;font-size:14px
}
.routeExportTemplate .routeNav .routeNavHeader .checkbox{float:right;margin-right:15px;margin-top:12px
}
.routeExportTemplate .routeNav .routeNavHeader .checkbox label{display:inline-block;margin-right:10px;font-size:14px
}
.routeExportTemplate .routeNav .routeNavHeader .checkbox .checkboxC{display:inline-block;vertical-align:middle
}
.routeExportTemplate .routeNav li{position:relative;min-height:72px;padding:12px 40px 12px 10px;border-bottom:1px solid #EBEEF2;cursor:pointer
}
.routeExportTemplate .routeNav li .day{float:left;line-height:36px;font-size:32px;color:#C8D0D5
}
.routeExportTemplate .routeNav li .cityList{clear:both;overflow:hidden;padding-top:5px;font-size:14px
}
.routeExportTemplate .routeNav li .cityList .item{float:left
}
.routeExportTemplate .routeNav li .cityList .item .cityName{float:left;position:relative
}
.routeExportTemplate .routeNav li .cityList .item .cityName .delCity{display:none;position:absolute;right:-5px;top:-12px
}
.routeExportTemplate .routeNav li .cityList .item .cityName .delCity i{font-size:14px;color:#F56C6C
}
.routeExportTemplate .routeNav li .cityList .item .cityName .delCity i:hover{color:red
}
.routeExportTemplate .routeNav li .cityList .item .cityName.init{padding:0 5px;border:1px dashed #ccc
}
.routeExportTemplate .routeNav li .cityList .item .cityName:hover .delCity{display:block
}
.routeExportTemplate .routeNav li .cityList .item .arrow{float:left;padding:0 5px
}
.routeExportTemplate .routeNav li .cityList .item:last-child .arrow{display:none
}
.routeExportTemplate .routeNav li .checkboxC{cursor:pointer;position:absolute;right:16px;top:16px
}
.routeExportTemplate .routeNav li .checkboxC.active{display:block
}
.routeExportTemplate .routeNav li.active{background:#F7F8F9
}
.routeExportTemplate .routeNav li.active:hover{background:#F7F8F9
}
.routeExportTemplate .routeNav li.active .day{color:#666
}
.routeExportTemplate .routeNav li:hover{background:#fdfdfd
}
.routeExportTemplate .routeNav li:hover .checkboxC{display:block
}
.routeExportTemplate .routeDayDetail{height:500px;padding-top:15px;margin-left:300px;overflow-y:auto;background:#fff;line-height:22px;font-size:13px
}
.routeExportTemplate .footer{height:60px;background:#E2E2E2
}
.routeExportTemplate .footer .btns{float:right;margin:14px 30px 0 0
}
.routeExportTemplate .footer .btns .el-button{width:70px;margin-left:5px
}


