
.trafficBox{left:620px;top:80px;bottom:20px;width:400px;transition:all .2s ease
}
.trafficBox.setDayPoiEditActive{left:340px
}
.trafficBox .header{padding-right:0
}
.trafficBox .header .tit{font-size:14px
}
.trafficBox .header .actionBtn{float:left;margin-right:15px
}
.trafficBox .header .closeBtn{float:left;display:block;width:54px;height:54px;line-height:54px;border-top:1px solid #e2e2e2;transform:rotate(-90deg);text-align:center;color:#999
}
.trafficBox .header .closeBtn:hover{background:#f1f1f1
}
.trafficBox .header .closeBtn i{font-size:26px
}
.trafficBox .typeSel .tit{padding:10px 15px;line-height:30px;font-size:14px;color:#999
}
.trafficBox .typeSel .typeList{padding:0 15px;overflow:hidden;border-bottom:1px solid #e2e2e2
}
.trafficBox .typeSel .typeList li{float:left;width:25%;padding:10px 0 20px;text-align:center;color:#A7AFB4;cursor:pointer
}
.trafficBox .typeSel .typeList li.active,.trafficBox .typeSel .typeList li:hover{color:#23a16d
}
.trafficBox .typeSel .typeList li i{font-size:24px
}
.trafficBox .typeSel .typeList li span{line-height:24px;font-size:14px;display:block
}
.trafficBox .typeSel .summaryInfo{position:relative;margin:20px 15px;padding:15px;border:1px solid #e2e2e2;cursor:pointer
}
.trafficBox .typeSel .summaryInfo:hover{background:#F7F8F9
}
.trafficBox .typeSel .summaryInfo h3{font-size:16px
}
.trafficBox .typeSel .summaryInfo p{margin-top:5px;font-size:14px
}
.trafficBox .typeSel .summaryInfo p span{margin-right:5px
}
.trafficBox .typeSel .summaryInfo .addToSchedule{position:absolute;top:50%;margin-top:-15px;right:15px;height:30px;width:30px;border:2px solid #23a16d;color:#23a16d;line-height:30px;border-radius:100%;text-align:center
}
.trafficBox .typeSel .summaryInfo .addToSchedule:hover{background:#23a16d;color:#fff
}
.trafficBox .typeSel .summaryInfo .addToSchedule i{font-size:18px;font-weight:bold
}
.trafficBox .trafficDetail h3{padding:10px 15px 0;line-height:32px;color:#999;font-weight:normal
}
.trafficBox .trafficDetail .summaryInfo{padding:10px 15px 15px;overflow:hidden;border-bottom:1px solid #EAEDF1
}
.trafficBox .trafficDetail .summaryInfo .icon{float:left
}
.trafficBox .trafficDetail .summaryInfo .icon i{font-size:26px;color:#23a16d
}
.trafficBox .trafficDetail .summaryInfo .info{margin-left:35px;font-size:14px
}
.trafficBox .trafficDetail .summaryInfo p{margin-left:35px;font-size:13px;color:#999
}
.trafficBox .trafficDetail .tit{height:51px;padding:0 15px;line-height:50px;border-bottom:1px solid #EAEDF1;font-size:14px
}
.trafficBox .trafficDetail .tit .back{float:left
}
.trafficBox .trafficDetail .tit .back i{font-size:22px
}
.trafficBox .trafficDetail .tit .summary{float:left;margin-left:10px
}
.trafficBox .trafficDetail .tit .summary i{color:#23a16d
}
.trafficBox .trafficDetail .tit .add{float:right;height:30px;width:30px;margin-top:10px;border:2px solid #23a16d;color:#23a16d;line-height:30px;border-radius:100%;text-align:center
}
.trafficBox .trafficDetail .tit .add:hover{background:#23a16d;color:#fff
}
.trafficBox .trafficDetail .tit .add i{font-size:18px;font-weight:bold
}
.trafficBox .trafficDetail .detail{position:relative;padding:10px 15px 15px;font-size:14px
}
.trafficBox .trafficDetail .detail:before{content:'';position:absolute;left:27px;top:31px;bottom:36px;width:0;border-right:1px dashed #A7AFB4
}
.trafficBox .trafficDetail .detail .startPoint{position:relative;line-height:30px
}
.trafficBox .trafficDetail .detail .startPoint i{vertical-align:middle;color:#23a16d;font-size:24px
}
.trafficBox .trafficDetail .detail .endPoint{position:relative;line-height:30px
}
.trafficBox .trafficDetail .detail .endPoint i{vertical-align:middle;font-size:24px;color:#FB5252
}
.trafficBox .trafficDetail .detail li{position:relative;padding:10px 0
}
.trafficBox .trafficDetail .detail li i{float:left;margin-left:4px;background:#fff;font-size:18px
}
.trafficBox .trafficDetail .detail li .title{margin-left:30px
}
.trafficBox .trafficDetail .detail li .des{margin-left:30px;padding:8px 0;color:#999;font-size:13px
}
.trafficBox .demoInfo{padding:15px
}
.trafficBox .demoInfo .pic{width:100%;height:160px;text-align:center;background-size:cover;background-position:center
}
.trafficBox .demoInfo .hotelCheckTime{margin:10px 0
}
.trafficBox .demoInfo .hotelCheckTime label{color:#a0abb3
}
.trafficBox .demoInfo .hotelCheckTime label:last-child{margin-left:50px
}
.trafficBox .demoInfo .hotelRemark{margin:15px 0
}
.trafficBox .demoInfo .hotelRemark label{float:left;color:#a0abb3
}
.trafficBox .demoInfo .hotelRemark label:last-child{margin-left:50px
}
.trafficBox .demoInfo .hotelRemark .text{margin-left:60px
}
.trafficBox .demoInfo .title{margin-top:10px
}
.trafficBox .demoInfo .title h2{font-weight:normal;font-size:20px;color:#23a16d
}
.trafficBox .demoInfo .des{margin-top:5px;line-height:24px
}
.trafficBox .demoInfo .detail{margin-top:15px;padding-top:10px;border-top:1px solid #e2e2e2
}
.trafficBox .demoInfo .detail li{overflow:hidden;padding:8px 0
}
.trafficBox .demoInfo .detail li label{float:left;width:85px;color:#999
}
.trafficBox .demoInfo .detail li .txt{margin-left:85px
}
.trafficMap{z-index:1;left:725px;right:20px;top:80px;bottom:20px
}
.trafficMap .header .opts{height:54px;margin-right:-15px;border-left:1px solid #EAEDF1
}
.trafficMap .header .opts:hover{background:#f1f1f1
}
.trafficMap .header .opts .closeBox{display:block;width:54px;line-height:54px;text-align:center
}
.trafficMap .header .opts .closeBox i{font-size:20px;font-weight:bold;color:#A1ACB3
}
.trafficMap .googleMap{position:absolute;top:55px;left:0;right:0;bottom:0
}
.mapTrafficMarker{position:relative;width:20px;height:20px;background:#23a16d;border-radius:100%;color:#fff;text-align:center;line-height:20px;font-size:14px
}


