
.selectBox{position:relative;z-index:100
}
.selectBox span{padding-left:10px;line-height:32px
}
.selectBox .list{display:none;position:absolute;top:32px;background:#fff;box-shadow:0 2px 12px 0 rgba(0,0,0,0.1);border:1px solid #e2e2e2
}
.selectBox .list.ulLeft{left:0
}
.selectBox .list.ulRight{right:0
}
.selectBox .list li{min-width:120px;padding:5px 10px;line-height:22px;cursor:pointer
}
.selectBox .list li.active{background:#E3EBED
}
.selectBox .list li.active:hover{background:#E3EBED
}
.selectBox .list li:hover{background:#F5F7FA
}
.selectBox:hover .list{display:block
}


