
.mainHeader{height:60px;background:#fff;box-shadow:0 3px 10px rgba(0,0,0,0.04)
}
.mainHeader .toggle{float:left;width:70px;height:60px;background:url(../../assets/images/icon_main_nav_toggle.png) center center no-repeat;border-right:1px solid #ebebeb;cursor:pointer
}
.mainHeader .searchBox{float:left;width:370px;height:36px;margin-top:12px;margin-left:18px;background:#EDF1F2;border-radius:18px
}
.mainHeader .searchBox.mainRoutePage{width:250px
}
.mainHeader .searchBox.mainRoutePage .iconMapPoint,.mainHeader .searchBox.mainRoutePage .cityKeywords{display:none
}
.mainHeader .searchBox.mainRoutePage .keywords{border-left:0
}
.mainHeader .searchBox .iconMapPoint{float:left;width:16px;height:18px;margin:9px 0 0 15px;background:url(../../assets/images/icon_map_point.png)
}
.mainHeader .searchBox .el-icon-search{display:none;float:left;margin:10px 0 0 5px;color:#9FA5B0;font-size:16px
}
.mainHeader .searchBox .cityKeywords{float:left;width:100px;height:36px;margin-left:10px;background:none;border:0;line-height:36px;font-size:13px
}
.mainHeader .searchBox .keywords{float:left;width:175px;height:24px;margin-left:10px;background:none;border:0;margin-top:6px;padding-left:8px;border-left:1px solid #ddd;line-height:24px;font-size:13px
}
.mainHeader .searchBox .btnSearch{float:right;width:42px;height:36px;background:url(../../assets/images/icon_search.png) 10px center no-repeat
}
.mainHeader .optsBox{float:right
}
.mainHeader .changeLanguage{float:left;padding:0 22px;line-height:60px
}
.mainHeader .msg{float:left;width:90px;height:60px;background:url(../../assets/images/icon_msg.png) center center no-repeat
}
.mainHeader .msg .num{float:left;height:16px;margin-left:44px;margin-top:14px;padding:0 6px;line-height:16px;background:#F83B3B;border-radius:16px;color:#fff
}
.mainHeader .user{float:left;padding:0 22px;line-height:60px;font-size:14px
}
.mainHeader .user .name{position:relative;z-index:100000000;float:left
}
.mainHeader .user .name .menu{display:none;position:absolute;z-index:100;top:50px;right:0;width:135px;padding:12px 0;background:#fff;box-shadow:0 5px 15px rgba(0,0,0,0.2)
}
.mainHeader .user .name .menu li{overflow:hidden;line-height:36px;padding-left:18px;cursor:pointer
}
.mainHeader .user .name .menu li i{display:inline-block;vertical-align:middle;margin-top:-3px;margin-right:12px
}
.mainHeader .user .name .menu li:hover{background:#E9F1F1
}
.mainHeader .user .name:hover .menu{display:block
}
.mainHeader .user .avatar{float:left;height:42px;margin-left:10px;margin-top:9px
}
.mainHeader .user .avatar .pic{width:45px;height:45px;background-size:cover;background-position:center;border-radius:100%
}


