
.daySchedule{width:300px;top:80px;bottom:20px;left:300px;transition:all .3s ease
}
.daySchedule.active{left:180px
}
.daySchedule.setDayPoiEditActive{left:20px
}
.daySchedule .nullTip{padding-bottom:30px;text-align:center
}
.daySchedule .nullTip .icon{width:70px;height:70px;margin:100px auto 15px;background:url(../../assets/images/icon_null.png)
}
.daySchedule .nullTip p{margin-bottom:40px;color:#637a8c;font-size:14px
}
.daySchedule .nullTip p a{color:#0079f5
}
.daySchedule .nullTip .el-button{width:150px;border-color:#23a16d;color:#23a16d
}
.daySchedule .nullTip .el-button i{font-weight:bold
}
.dayCityList{min-height:51px;padding:10px 15px;border-bottom:1px solid #EAEDF1;line-height:32px
}
.dayCityList .cityList .item{float:left
}
.dayCityList .cityList .item .cityName{float:left;position:relative;font-size:14px
}
.dayCityList .cityList .item .cityName .delCity{display:none;position:absolute;right:-7px;top:-14px;cursor:pointer
}
.dayCityList .cityList .item .cityName .delCity i{font-size:16px;color:#F56C6C
}
.dayCityList .cityList .item .cityName .delCity i:hover{color:red
}
.dayCityList .cityList .item .cityName.init{padding:0 5px;border:1px dashed #ccc
}
.dayCityList .cityList .item .cityName:hover .delCity{display:block
}
.dayCityList .cityList .item .arrow{float:left;padding:0 5px
}
.dayCityList .cityList .item:last-child .arrow{display:none
}
.dayCityList .addCity{float:left;color:#999;font-size:14px
}
.dayCityList .addCity:hover{color:#555
}
.dayCityList .addCity i{margin-left:10px;font-size:14px
}
.dayCityList .addCityCon{float:left
}
.dayCityList .addCityCon .arrow{float:left;padding:0 5px
}
.dayCityList .addCityCon .citySelect{float:left
}
.dayCityList .addCityCon .el-input__icon{display:none
}
.dayCityList .addCityCon .el-input__inner{width:85px;padding-left:5px;border:0;font-size:13px;color:#637a8c
}
.scheduleList{position:absolute;top:51px;bottom:0;left:0;right:0;overflow-y:auto
}
.scheduleList .scheduleCon{position:relative
}
.scheduleList .scheduleCon.singleItem .itemWrap .item:before{display:none
}
.scheduleList .scheduleCon .itemWrap:first-child .item:before{top:45px
}
.scheduleList .scheduleCon .itemWrap:last-child .item:before{height:20px
}
.scheduleList .scheduleCon .itemWrap:last-child .item.traffic{display:none
}
.scheduleList .scheduleCon .itemWrap:last-child .item.product .no{background:#AFA37A
}
.scheduleList .scheduleCon .itemWrap:last-child .item.product .con .tit i{color:#AFA37A
}
.scheduleList .scheduleCon .itemWrap:last-child .item.hotel .no{background:blue
}
.scheduleList .scheduleCon .itemWrap .item{position:relative;padding:10px 15px;overflow:hidden;border-bottom:1px solid #EAEDF1
}
.scheduleList .scheduleCon .itemWrap .item:before{content:'';position:absolute;left:33px;top:0;width:0;height:100%;border-left:1px dashed #DBE0E3
}
.scheduleList .scheduleCon .itemWrap .item:hover{background:#F7F8F9
}
.scheduleList .scheduleCon .itemWrap .item:hover .del{display:block
}
.scheduleList .scheduleCon .itemWrap .item .no{position:absolute;top:18px;left:18px;width:30px;height:30px;border-radius:15px;background:#23a16d;line-height:30px;color:#fff;text-align:center;font-size:16px
}
.scheduleList .scheduleCon .itemWrap .item .noHotel{background:none;color:#23a16d
}
.scheduleList .scheduleCon .itemWrap .item .noHotel i{font-size:24px
}
.scheduleList .scheduleCon .itemWrap .item .noCar{background:#EBEDF1;color:#23a16d
}
.scheduleList .scheduleCon .itemWrap .item .noCar i{font-size:24px
}
.scheduleList .scheduleCon .itemWrap .item .noTraffic{background:#23a16d url(../../assets/images/icon_traffic.png) center center no-repeat
}
.scheduleList .scheduleCon .itemWrap .item .con{margin-left:48px;cursor:pointer
}
.scheduleList .scheduleCon .itemWrap .item .con .tit{padding:5px 0
}
.scheduleList .scheduleCon .itemWrap .item .con .tit i{color:#23a16d
}
.scheduleList .scheduleCon .itemWrap .item .con .tit label{color:#a0abb3
}
.scheduleList .scheduleCon .itemWrap .item .con .titEn{padding:5px 0;color:#a0abb3
}
.scheduleList .scheduleCon .itemWrap .item .con .price{padding:5px 0
}
.scheduleList .scheduleCon .itemWrap .item .con.trafficInfo{position:relative
}
.scheduleList .scheduleCon .itemWrap .item .con.trafficInfo:before{content:'';position:absolute;left:6px;top:17px;bottom:32px;width:0;border-left:1px dashed #DBE0E3
}
.scheduleList .scheduleCon .itemWrap .item .con.trafficInfo .startPoint i{float:left;width:13px;height:13px;margin-top:4px;border-radius:100%;border:2px solid #23a16d
}
.scheduleList .scheduleCon .itemWrap .item .con.trafficInfo .startPoint .city{margin-left:20px
}
.scheduleList .scheduleCon .itemWrap .item .con.trafficInfo .startPoint .point{margin-left:20px;color:#a0abb3
}
.scheduleList .scheduleCon .itemWrap .item .con.trafficInfo .type{position:relative;z-index:1;margin:15px 40px 15px 0
}
.scheduleList .scheduleCon .itemWrap .item .con.trafficInfo .type .icon{float:left
}
.scheduleList .scheduleCon .itemWrap .item .con.trafficInfo .type .icon i{background:#fff;color:#23a16d
}
.scheduleList .scheduleCon .itemWrap .item .con.trafficInfo .type .model{display:block;margin-left:20px
}
.scheduleList .scheduleCon .itemWrap .item .con.trafficInfo .endPoint i{float:left;width:13px;height:13px;margin-top:4px;border-radius:100%;border:2px solid #FA574B
}
.scheduleList .scheduleCon .itemWrap .item .con.trafficInfo .endPoint .city{margin-left:20px
}
.scheduleList .scheduleCon .itemWrap .item .con.trafficInfo .endPoint .point{margin-left:20px;color:#a0abb3
}
.scheduleList .scheduleCon .itemWrap .item .del{display:none;position:absolute;top:50%;right:15px;width:20px;height:20px;margin-top:-10px;line-height:20px;color:#D7DEE2;cursor:pointer
}
.scheduleList .scheduleCon .itemWrap .item .del i{font-size:22px
}
.scheduleList .scheduleCon .itemWrap .item .del:hover{color:#F56C6C
}
.scheduleList .scheduleCon .itemWrap .item.traffic{z-index:1;background:#fff;padding-left:27px;cursor:pointer
}
.scheduleList .scheduleCon .itemWrap .item.traffic:before{display:none
}
.scheduleList .scheduleCon .itemWrap .item.traffic i{margin-right:5px
}
.scheduleList .scheduleCon .itemWrap .item.traffic:hover{background:#F7F8F9
}
.scheduleList .scheduleCon .itemWrap .item.active{background:#F7F8F9
}
.notes.day{left:620px
}
.notes.day.poiActive:before{content:'';position:absolute;z-index:10;top:0;left:0;right:0;bottom:0;background:rgba(0,0,0,0.02)
}
.dayPercent{float:left;position:relative;margin:12px
}
.dayPercent .dayPercentTxt{position:absolute;left:0;top:0;width:30px;height:30px;line-height:30px;text-align:center
}


