
.poiTypeFilter{float:left;margin-right:30px
}
.mainPoiList .tableList{overflow:hidden;margin-right:-30px
}
.mainPoiList .tableList li{float:left;width:25%;margin-bottom:30px
}
.mainPoiList .tableList li .inside{cursor:pointer;margin-right:30px;border:1px solid #eee
}
.mainPoiList .tableList li .inside:hover{border-color:#e2e2e2
}
.mainPoiList .tableList li .inside .pic{overflow:hidden
}
.mainPoiList .tableList li .inside .pic .img{width:100%;height:0;padding-bottom:56.25%;background-size:cover;background-position:center;transition:all .3s
}
.mainPoiList .tableList li .inside .pic .img:hover{transform:scale(1.2)
}
.mainPoiList .tableList li .inside .info{position:relative;height:64px;padding:10px
}
.mainPoiList .tableList li .inside .info .tit{width:100%;overflow:hidden;white-space:nowrap;text-overflow:ellipsis;font-size:16px
}
.mainPoiList .tableList li .inside .info .tit span{color:#23a16d
}
.mainPoiList .tableList li .inside .info .subTit{width:100%;padding-right:20px;overflow:hidden;white-space:nowrap;text-overflow:ellipsis;color:#a0abb3
}
.mainPoiList .tableList li .inside .info .del{position:absolute;bottom:10px;right:10px;color:#a0abb3
}
.mainPoiList .tableList li .inside .info .del i{font-size:16px
}
.mainPoiList .tableList li .inside .info .del:hover{color:#555
}
.mainPoiList .el-pagination{padding-top:0
}
.geoInfo{height:300px;border:1px solid #e2e2e2
}
.geoInfo .geoKeyword{position:absolute;z-index:100;top:12px;left:12px;width:300px
}
.geoInfo .geoMap{width:100%;height:300px
}
.poiDetailImg{width:100%;height:200px;background-size:cover;background-position:center
}
.poiDetailForm{margin-top:20px
}
.poiDetailForm .poiType{color:#23a16d
}
.poiDetailForm .el-form-item__label{color:#999
}
.poiDetailForm .el-form-item{margin-bottom:8px
}


